import React from 'react'
import {List} from './list'
import SearchPanel from './search-panel'
import {useDebounce, useDocmentTitle} from '../../utils'
import styled from '@emotion/styled'
import { Button,} from 'antd'
import { useProjects } from 'utils/project'
import { useUsers } from 'utils/user'
import { useProjectModal, useProjectsSearchParams } from './util'
import { ErrorBox, Row } from 'components/lib'



// ${qs.stringify(cleanObject(param))}   name=${param.name}&personId=${param.personId}
export default function ProjectListScreen() {
  useDocmentTitle('项目管理',false)

    const {open} = useProjectModal()
    const [param, serParam] = useProjectsSearchParams()   
    // console.log(param);
       
    const {isLoading,error,data:list} = useProjects(useDebounce(param,200))
    // const {run,isLoading,error,data:list} = useAsync<Project[]>()

    // useEffect(() =>{
    //     run(client('projects',{data:cleanObject(debounceParam)}))
    //     // client('projects',{data:cleanObject(debounceParam)}).then(setList)

    // },[debounceParam])  

    const {data:users} = useUsers()
    // useMount(() =>{
    //     client('users').then(setUsers)
    // })
  return (
    <Container>
        <Row between={true}>
        <h1>项目列表</h1>
        <Button onClick={open} type={"link"}>创建项目</Button>
        </Row>
        <SearchPanel param={param} setParam={serParam} users={users || []}/>
        <ErrorBox error={error}/> 
        <List  loading={isLoading} dataSource={list || []} users={users || []}/>
    </Container>
  )
}

//在class组件中，static whyDidYouRender = true
ProjectListScreen.whyDidYouRender = false


const Container = styled.div`
    padding: 3.2rem;
    width: 100%;
`