import { useMemo, useState } from "react"
import { URLSearchParamsInit, useSearchParams } from "react-router-dom"
import { cleanObject } from "utils"

export const useUrlQueryParam =<K extends string>(keys:K[]) => {
    //useSearchParams()返回当前的seaech参数、更新search的函数
    const [searchParams] = useSearchParams()
    const setSearchParams = useSetUrlSearchParam()
    const [stateKeys] = useState(keys)
    return [
        useMemo(
            () => keys.reduce((prev, key) => {
                return{...prev, [key]: searchParams.get(key) || ''}
            },{} as { [key in K]:string}),
            //eslint-disable-next-line react-hooks/exhaustive-deps
            [searchParams,stateKeys]
            ),
            (params: Partial<{ [key in K]: unknown }>) => {
                return setSearchParams(params)
            }
    ] as const
}

export const useSetUrlSearchParam = () => {
    const [searchParams,setSearchParam] = useSearchParams()
    return (params: {[key in string]: unknown}) => {
        //Object.fromEntries() 方法把键值对列表转换为一个对象。
        //给无法for of 的数据添加iterator: https://codesandbox.io/s/upbeat-wood-bum3j?file=/src/index.js
        const o = cleanObject({...Object.fromEntries(searchParams),
            ...params}) as URLSearchParamsInit
        return setSearchParam(o)
    }
}