import { Form, Input, } from 'antd';
import { UserSelect } from 'components/use-select';
import React from 'react';
import { Project } from './list';
export interface User{
    id: number,
    name:string,
    email:string,
    title:string,
    organization:string,
    token:string
}

interface SearchPanelProps{
    param: Partial<Pick<Project,'name' | 'personId'>>
    setParam:(param:SearchPanelProps['param']) => void,
    //表示users是一个Use类型的数组
    users:User[]
}

export default function SearchPanel({param,setParam,users}:SearchPanelProps) {
    // console.log('search',param);
  return (
    <Form style={{ marginBottom: "2rem" }} layout={'inline'} >
        <Form.Item>
            <Input 
                placeholder={'项目名'}
                type="text" value={param.name} onChange={evt => setParam(
                {...param,
                name: evt.target.value
                }            
            )}/>
        </Form.Item>
        <Form.Item>
            <UserSelect
            defaultOptionName='负责人'
            value={param.personId} onChange={value => setParam(
                {...param,
                    personId: value
                }            
            )}/>
            {/* <Select value={param.personId} onChange={value => setParam(
                {...param,
                    personId: value
                }            
            )} >
                <Select.Option value="">负责人</Select.Option>
                {
                    users.map(user => {
                        return (
                            <Select.Option  key={user.id} value={String(user.id)}>                               
                                {user.name}
                            </Select.Option>
                        )
                    })
                }
            </Select> */}
        </Form.Item>
    </Form >
  )
}
