
import { Project } from "screens/project-list/list"
import {  } from "utils"
import { useHttp } from "./http"
import { QueryKey, useMutation, useQuery,} from 'react-query'
import { useEditConfig , useAddConfig, useDeleteConfig} from "./use-optimistic-options"

//Partial类型转换https://blog.csdn.net/roamingcode/article/details/104111165
//用来获取project列表
export const useProjects = (param?:Partial<Project>) => {
    const client = useHttp()

    return useQuery<Project[]>(['projects',param],() => client(`projects`, {data:param}))
 
}

//向服务端发送编辑请求
export const useEditProject = (queryKey: QueryKey) => {
    const client = useHttp()
    return useMutation(
        (params:Partial<Project>) => client(`projects/${params.id}`,{
            data:params,
            method:'PATCH'
        }),
        useEditConfig(queryKey)
        )
}

export const useAddProject = (queryKey: QueryKey) => {
    const client = useHttp()
    return useMutation(
        (params:Partial<Project>) => client(`projects`,{
            data:params,
            method:'POST'
        }),
        useAddConfig(queryKey)
        )
}

export const useDeleteProject = (queryKey: QueryKey) => {
    const client = useHttp()
    return useMutation(
        ({id}:{id :number}) => client(`projects/${id}`,{
            method:'DELETE'
        }),
        useDeleteConfig(queryKey)
        )
}



//获取projects详情
export const useProject = (id?: number) => {
    const client = useHttp()
    return useQuery<Project>(
        ['project', {id}],
        () => client(`projects/${id}`),
        //id存在时才执行上面的代码
        {
            enabled: Boolean(id)
        }
    )
}